import React from 'react'
import { connect } from 'react-redux'
import { getAllOrder, getAllPrice, getDexData } from '../../redux/actions'
import {
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
  Bar,
  ComposedChart,
  Line
} from 'recharts'
import web3instance from '../../connector/web3'
import { mergeTwoData } from '../../utils/graph/cleanData'

class Graph extends React.Component {
  state = {
    data: []
  }
  componentDidMount() {
    this.props.getDexData()
    const web3 = web3instance(this.props.web3)
    console.log('graph mount')
    this.props.getAllOrder(web3).then(() => {
      console.log('volume', this.props.volumeData)
      this.props.getAllPrice(web3).then(() => {
        const data = mergeTwoData(this.props.volumeData, this.props.priceData)
        console.log(data)
        this.setState({ data })
      })
    })
  }

  render() {
    return (
      <div className="container-fluid" style={{ marginTop: '100px' }}>
        <ComposedChart width={1000} height={400} data={this.state.data}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="timeStamp" />
          <YAxis yAxisId="left" />
          <YAxis yAxisId="right" orientation="right" />
          <Tooltip />
          <Legend />
          <Bar dataKey="total" unit=" ETH" fill="#3a2bac" yAxisId="right" />
          <Line
            dataKey="HODOLprice"
            unit=" USD"
            type="monotone"
            stroke="#00bfc7"
            yAxisId="left"
            dot={false}
            strokeWidth="3px"
          />
        </ComposedChart>
      </div>
    )
  }
}

const mapStateToProps = ({ graph }) => {
  return {
    volumeData: graph.volume,
    priceData: graph.price
  }
}

export default connect(mapStateToProps, {
  getAllOrder,
  getAllPrice,
  getDexData
})(Graph)
